.navbar {
    position: fixed;
    top: 0;
    z-index: 1;
    display: flex;
    width: 100%;
    height: 40px;
    background: rgba(0, 0, 0, 0.9);
}

.navbar ul {
    display: flex;
    list-style: none;
    width: 100%;
    padding: 0;
    justify-content: center;
    margin: 8px 0 8px 0;
}

.navbar ul li {
    margin: 0 1rem;
    padding: 0 1rem 0 1rem;
    text-decoration: none;
    text-transform: uppercase;
    color: rgb(210, 210, 210);
    transition: color 0.2s ease-in-out;
    user-select: none;
    font-weight: 600;
}

.navbar ul li:hover {
    color: rgb(105, 205, 105);
    cursor: pointer;
    transition: color 0.2s ease-in-out;
}

@media (max-width: 769px) {
    .navbar {
        display: none;
    }
}
